import React, { Component } from 'react';
import Expenseitem from "./Expenseitem";
import Expensesfilter from "./Expensefilter";
import Expenseschart from './Expenseschart';
import "./css/Expenses.css";

class Expenses extends Component {
  constructor(props) {
    super(props);
    this.state = {
      filteredYear: '2024'
    };
  }

  filterChangeHandler = (selectedYear) => {
    this.setState({ filteredYear: selectedYear });
  };

  render() {
    const filteredExpenses = this.props.items.filter((expense) => {
      return expense.date.getFullYear().toString() === this.state.filteredYear;
    });

    let emptyfeed = <p>You Need To Add Details !</p>;

    if (filteredExpenses.length > 0) {
      emptyfeed = filteredExpenses.map((item) => (
        <Expenseitem
          key={item.id}
          title={item.title}
          amount={item.amount}
          date={item.date}
        />
      ));
    }

    return (
      <div className="expenses">
        <Expensesfilter selected={this.state.filteredYear}
        onChangeFilter={this.filterChangeHandler} />
        <Expenseschart expenses={filteredExpenses} /> 
        {emptyfeed}
      </div>
    );
  }
}

export default Expenses;



