// Expenseform.js
import React, { useState } from "react";
import "./css/expenseform.css";

const Expenseform = (props) => {
  const [enterdate, setenterdate] = useState('');
  const [entertitle, setentertitle] = useState('');
  const [enteramount, setenteramount] = useState('');

  const datechangeevent = (event) => {
    setenterdate(event.target.value);
  };

  const titlechangeevent = (event) => {
    setentertitle(event.target.value);
  };

  const amountchangeevent = (event) => {
    setenteramount(event.target.value);
  };

  const submithandler = (event) => {
    event.preventDefault();

    const expensedata = {
      title: entertitle,
      amount: +enteramount,
      date: new Date(enterdate),
    };
    props.onsave(expensedata);

    setenterdate('');
    setenteramount('');
    setentertitle('');
  };

  return (
    <form onSubmit={submithandler}>
      <div className="new-expense__controls">
        <div className="new-expense__control">
          <label>Expense Date</label>
          <input type="date" min="2023-01-01" max="2025-12-31" value={enterdate} onChange={datechangeevent} />
        </div>
        <div className="new-expense__control">
          <label>Expense Details</label>
          <input type="text" value={entertitle} onChange={titlechangeevent} /> 
        </div>
        <div className="new-expense__control">
          <label>Expense Amount ⟨₹⟩</label>
          <input type="number" min="0.01" step="0.01" value={enteramount} onChange={amountchangeevent} />
        </div>
      </div>
      <div className="new-expense__actions">
        <button type="submit">Add Expense</button>
      </div>
    </form>
  );
};

export default Expenseform;



